import React from "react"

import classNames from "classnames";
import _ from "lodash";
class CompanyBox extends React.Component{
  render(){
    var info = (this.props.tag || "").split(' ');
    var boxList = _.map(info, (tag)=>{
      return <td>
        <p style={{'paddingLeft':'2px'}}></p>
        <div id="label-box"
        className={
          classNames({
            'color-orange': tag == ""|| tag == "5G"|| tag == "AI"|| tag == ""|| tag ==""||tag == ""||tag ==""||tag == "",
            'color-green' : tag == ""|| tag == ""||tag == ""||tag == "" || tag == "" || tag == ""||tag == "",
            'color-red' : tag == ""|| tag ==""|| tag == ""||tag ==""||tag==""||tag=="",
            'color-blue' :tag == ""||tag == ""||tag == "" || tag =="" || tag =="",
            'color-purple':  tag == "" || tag ==""|| tag == "3D" || tag == "" ||tag=="",
            'color-yellow': tag=="" || tag == "" ||tag=="" || tag =="" ,
          })}>
          <p className="label-text">{tag}</p>
        </div>
      </td>
    })
    return(
      <div>
        <table border="0">
          <tbody>
            <tr>
              <td  id="space-box"
              style={{'paddingLeft':'10px'}}></td>
              {boxList}
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default CompanyBox;